import { Component, OnInit, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import {IonText,IonChip,IonItem,IonList,IonCard, IonCardContent,IonCardHeader, IonCardSubtitle,IonLabel,IonCol,IonRow,IonGrid,IonListHeader,IonButtons,IonBackButton,IonContent, IonHeader, IonTitle, IonToolbar } from '@ionic/angular/standalone';
import { Router } from '@angular/router';

import { PlyrPlayerComponent } from '../../components/plyr-player/plyr-player.component';


@Component({
  selector: 'app-ricetta',
  templateUrl: './ricetta.page.html',
  styleUrls: ['./ricetta.page.scss'],
  standalone: true,
  imports: [PlyrPlayerComponent, IonText, IonChip, IonItem, IonList, IonCard, IonCardContent, IonCardHeader, IonCardSubtitle, IonLabel, IonCol, IonRow, IonGrid, IonListHeader, IonButtons, IonBackButton, IonContent, IonHeader, IonTitle, IonToolbar, CommonModule]
})
export class RicettaPage implements OnInit {
  public recipe: any ={titolo:""};
  public videoUrl: string = '';

  constructor(private router: Router) {}

  ngOnInit() {
    
  }

  ionViewWillEnter() {
    const currentState = this.router.lastSuccessfulNavigation;
    console.log("ionViewWillEnter ", currentState?.extras)
    if (currentState?.extras.state) {
      this.recipe = currentState.extras.state['data'];
      console.log(this.recipe);
      this.videoUrl = this.recipe.video
    } else {
      console.error('Nessuna ricetta passata');
    }  }
}
